import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import mockupHippoMeowV2 from "../../images/mockupHippoMeowV2.png"
import hippoMeowV2FontsImage from "../../images/hippo-meow-v2-fonts.png"
import Work from "../../components/work"
import EmptyLine from "../../components/empty-line"

export default () => {
  const { hippoMeowV2 } = useStaticQuery(graphql`
    query {
      hippoMeowV2: file(relativePath: { eq: "hippoMeowV2.png" }) {
        childImageSharp {
          fluid(maxWidth: 1200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  return (
    <Work
      title="Hippomeow"
      subTitle="Cake &amp; Cafe Shop Website"
      children={
        <React.Fragment>
          <div>
            A family cake and cafe shop website - nearly the same layout and
            content as the first design above. However, I turn it into a more
            classic and traditional interface.
          </div>
          <EmptyLine variant="Paragraph" />
          <div>
            The homepage imitates the interface of a shop in reality with wood
            walls, lean-to, neon signboard, and window. This brings a more
            traditional and cozier atmosphere for users.
          </div>
        </React.Fragment>
      }
      mockupImage={mockupHippoMeowV2}
      colors={["#04a37a", "#c10202", "#494949", "#fef9dc"]}
      fontImage={hippoMeowV2FontsImage}
      designFluidImages={[hippoMeowV2.childImageSharp.fluid]}
      hasSecondaryBackgroundColor
    />
  )
}
